.main-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;

  .header-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    flex-wrap: wrap;
    gap: 20px;
  }

  .logo-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .logo {
      margin: 0;
      font-size: 1.8em;
      font-weight: 700;
      color: white;
    }

    .tagline {
      margin: 0;
      font-size: 0.8em;
      opacity: 0.9;
      font-style: italic;
    }
  }

  .main-nav {
    display: flex;
    gap: 15px;
    align-items: center;
    flex-wrap: wrap;
  }

  .nav-link {
    color: white;
    text-decoration: none;
    padding: 8px 16px;
    border-radius: 25px;
    font-weight: 500;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 5px;

    &:hover {
      background-color: rgba(255, 255, 255, 0.2);
      transform: translateY(-2px);
    }

    &.active {
      background-color: rgba(255, 255, 255, 0.3);
      font-weight: 600;
    }

    &.btn-primary {
      background-color: #ff6b6b;
      font-weight: 600;

      &:hover {
        background-color: #ff5252;
        transform: translateY(-2px);
      }
    }

    &.btn-login {
      border: 2px solid rgba(255, 255, 255, 0.5);

      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
        border-color: white;
      }
    }
  }

  .user-section {
    display: flex;
    align-items: center;
    gap: 15px;
  }

  .user-menu {
    display: flex;
    align-items: center;
    gap: 15px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 8px 16px;
    border-radius: 25px;

    .user-name {
      font-weight: 500;
      font-size: 0.9em;
      display: flex;
      align-items: center;
      gap: 4px;

      .admin-badge {
        font-size: 16px;
        animation: glow 2s ease-in-out infinite alternate;
      }
    }

    .logout-btn {
      background: none;
      border: 1px solid rgba(255, 255, 255, 0.5);
      color: white;
      padding: 6px 12px;
      border-radius: 15px;
      cursor: pointer;
      font-size: 0.8em;
      transition: all 0.3s ease;

      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
        border-color: white;
      }
    }
  }

  @media (max-width: 768px) {
    .header-container {
      flex-direction: column;
      text-align: center;
      gap: 15px;
    }

    .main-nav {
      justify-content: center;
    }

    .user-section {
      justify-content: center;
    }
  }
}
